import { Component } from '@angular/core';
import { IViewComponentDropPredicateData } from '@helix/platform/view/designer';
import { RX_SHELL } from '@helix/platform/view/api';
@Component({
  selector: 'rx-shell-user-menu-design',
  templateUrl: './shell-user-menu-design.component.html',
  styleUrls: ['./shell-user-menu-design.component.scss']
})
export class RxShellUserMenuDesignComponent {
  dropPredicate(data: IViewComponentDropPredicateData): boolean {
    return data.draggedViewComponentDescriptor.type === RX_SHELL.navBar.menuItem;
  }
}
